.logo {
  flex-shrink: 0;

  @include hover {
    opacity: 0.7;
  }

  @include vp-767 {
    // align-self: center;
    margin-top: 5px;
  }
}

.logo__icon {
  display: block;

  @include vp-767 {
    display: none;
  }

  &--mobile {
    display: none;

    @include vp-767 {
      display: block;
    }
  }
}
